import React from 'react'
import Styles from './Styles/NavbarStyles.module.css'
import Brand from './Brand'
import Menus from './Menus'
import {Link} from 'react-router-dom'

function Navbar() {

  return (
    <React.Fragment>
      <header className={Styles.navBarContainer} id="siteNavbar">
        <Brand />
        <Menus />
      </header>
      <div className={Styles.navMenuMobile}>
        <div className={Styles.menuWrapperMobile}>
          <Link className="nav-link" to="/">
              Home
          </Link>
          <Link className="nav-link" to="/about">
              About
          </Link>
          <Link className="nav-link" to="/projects">
              Projects
          </Link>
          <Link className="nav-link" to="/contact">
              Contact
          </Link>
        </div>
      </div>
    </React.Fragment>
  )
}

window.addEventListener("scroll", () => {
  let val = window.scrollY;
  let header = document.getElementById('siteNavbar');
  if (val > 10) {
    header.className = `${Styles.navBarContainer} ${Styles.navBg}`;
    console.log();
  } else {
    header.className = Styles.navBarContainer;
    console.log(Styles.navBarContainer);
  }
});

export default Navbar
