import React from 'react'
import './Styles/AppStyles.css'
import {HashRouter,Switch,Route} from 'react-router-dom'
import NotFound from './components/NotFound/Notfound'
import Navbar from './components/Navigation/Navbar'
import HomeSection from './components/Pages/HomeSection'
import AboutSection from './components/Pages/AboutSection'
import ProjectsSection from './components/Pages/ProjectsSection'
import ContactsSection from './components/Pages/ContactsSection'
import ProjectsAll from './components/Pages/ProjectsAll'

export default function App() {
    return (
        <HashRouter>
            <Navbar/>
            <Switch>
                <Route exact path="/" component={HomeSection} />
                <Route exact path="/about" component={AboutSection} />
                <Route exact path="/projects" component={ProjectsSection} />
                <Route exact path="/contact" component={ContactsSection} />
                <Route exact path="/projects/all" component={ProjectsAll} />
                <Route component={NotFound} />
            </Switch>
        </HashRouter>
    )
}
